<template>
  <section class="user-details">
    <div>
      <h2></h2>
      <div v-for="review in reviews" :key="review._Id">
        <p>{{ review.txt }}</p>
      </div>
    </div>

    <button @click="backToMain">Back</button>
  </section>
</template>

<script>
export default {
  data() {
    return {
      user: null,
    }
  },
  created() {},
  computed: {
    getUser() {},
    reviews() {
      return this.$store.getters.reviews
    },
  },
  methods: {
    loadReview() {
      console.log(this.user)
      this.$store.dispatch({
        type: 'getReviews',
        userId: this.user._id,
      })
    },

    backToMain() {
      this.$router.push('/toys')
    },
  },
}
</script>
