<template>
  <section v-if="toy" class="toy-details">
    <div class="img-container">
      <img v-if="toy" :src="require('../assets/' + toy.url)" />
    </div>

    <form class="toy-edit" @submit.prevent="save">
      <label>New name ?</label>
      <input type="text" v-model="toy.name" />
      <label>Price ?</label>
      <input type="number" v-model="toy.price" />
      <label>On stock?</label>
      <input type="checkbox" v-model="toy.inStock" />

      <button>Save</button>
    </form>

    <button @click="backToMain">Back</button>
  </section>
</template>

<script>
import { toyService } from '../services/toy-service.js'
export default {
  data() {
    return {
      toy: null,
    }
  },
  created() {
    this.loadToy()
  },
  methods: {
    save() {
      this.$store
        .dispatch({
          type: 'updateToy',
          toy: JSON.parse(JSON.stringify(this.toy)),
        })
        .then(savedToy => {
          console.log('Saved')
          this.$router.push('/toys')
        })
    },
    loadToy() {
      const id = this.$route.params.toyId
      if (id) {
        toyService.getById(id).then(toy => {
          this.toy = JSON.parse(JSON.stringify(toy))
        })
      } else {
        this.toy = toyService.getEmptyToy()
      }
    },
    backToMain() {
      this.$router.push('/toys')
    },
  },
}
</script>
