<template>
  <div class="main-header">
    <section>
      <h1>Toys Are Us</h1>
    </section>
    <section class="nav-links">
      <router-link to="/">Home</router-link>
      <router-link to="/user-details">Your Profile</router-link>
      <router-link to="/toys">Toys</router-link>
      <router-link to="/about">About</router-link>
      <router-link to="/chart">Toys chart</router-link>
      <router-link v-if="!isLoggedIn" to="/login">Login/Sign up</router-link>
      <a v-else @click="logout">Log out</a>
    </section>
  </div>
</template>

<script>
import store from '../store/index'
export default {
  name: 'main-header',
  data() {
    return {
      loggedInUser: null,
    }
  },
  created() {},
  methods: {
    async logout() {
      const loggedOutUser = this.loggedInUser
      const isLoggedOut = await this.$store.dispatch('logout', {
        loggedOutUser,
      })
      if (isLoggedOut) {
        this.$router.push('/login')
      }
    },
  },
  computed: {
    isLoggedIn() {
      this.loggedInUser = store.state.loggedInUser
      return this.loggedInUser
    },
  },
}
</script>
