<template>
  <section>
    <div v-if="chatOpen" class="chat-container">
      <ul class="messages">
        <li v-for="(msg, idx) in msgs" :key="idx">
          <span>{{ msg.from }}:</span>{{ msg.txt }}
        </li>
      </ul>
      <form id="form" @submit.prevent="sendMsg">
        <input v-model="msg.txt" id="input" autocomplete="off" />
        <button>Send</button>
      </form>
      <el-button @click="chatOpen = !chatOpen" class="close-chat-btn"
        >X</el-button
      >
    </div>
    <div v-else>
      <el-button
        class="open-chat-ctn"
        @click="chatOpen = !chatOpen"
        type="success"
        >Open Chat</el-button
      >
    </div>
  </section>
</template>

<script>
import { socketService } from '../services/socket.service.js'
export default {
  props: ['toy'],
  data() {
    return {
      chatOpen: false,
      msg: { from: 'Me', txt: '' },
      msgs: [],
      user: this.getLoggedinUser,
    }
  },
  components: {},
  created() {
    // socketService.setup();
    console.log(this.toy)
    socketService.emit('chat topic', this.toy._id)
    socketService.on('chat addMsg', this.addMsg)
  },
  destroyed() {
    socketService.off('chat addMsg', this.addMsg)
    // socketService.terminate();
  },
  computed: {
    getLoggedinUser() {
      return this.$store.getters.getLoggedinUser
    },
  },
  methods: {
    addMsg(msg) {
      this.msgs.push(msg)
    },
    sendMsg() {
      console.log(this.user, 'TOY ')
      console.log('Sending', this.msg)
      socketService.emit('chat newMsg', this.msg)
      // TODO: next line not needed after connecting to backend
      // this.addMsg(this.msg)
      // setTimeout(()=>this.addMsg({from: 'Dummy', txt: 'Yey'}), 2000)
      this.msg = { from: 'Me', txt: '' }
    },
    changeTopic() {
      socketService.emit('chat topic', this.topic)
    },
  },
}
</script>

